import { useState, useMemo, useContext } from 'react';
import { FiSearch, FiSun, FiMoon } from 'react-icons/fi';
import {
  TextField,
  IconButton,
  ToggleButton,
  ToggleButtonGroup,
  FormControl,
  InputLabel,
} from '@mui/material';
import { ThemeProvider as MuiThemeProvider } from '@mui/material/styles';
import { lightTheme, darkTheme } from './theme';
import { ThemeContext } from './contexts/ThemeContext';
import ResultList from './components/ResultList';
import diseaseData from './data/disease.json';
import regionData from './data/region.json';
import medicationData from './data/medication.json';
import vaccinationData from './data/vaccination.json';
import etcData from './data/etc.json';

const categoryMap = {
  disease: '',
  region: '',
  medication: '',
  vaccination: '',
  malaria_domestic: '',
  malaria_international: '',
  vcjd: '',
  etc: '',
};

const allData = [
  ...diseaseData,
  ...medicationData,
  ...vaccinationData,
  ...etcData,
  ...regionData,
].map((item) => {
  const { restriction, category } = item;
  let restrictionType = 'default';
  let restrictionPeriodDays = 0;
  let condition = '';

  if (restriction) {
    restrictionType = restriction.type;
    condition = restriction.condition || '';

    switch (restriction.periodUnit) {
      case 'day':
        restrictionPeriodDays = restriction.periodValue;
        break;
      case 'week':
        restrictionPeriodDays = restriction.periodValue * 7;
        break;
      case 'month':
        restrictionPeriodDays = restriction.periodValue * 30; // Approximation
        break;
      case 'year':
        restrictionPeriodDays = restriction.periodValue * 365; // Approximation
        break;
      case 'permanent':
        restrictionPeriodDays = -1;
        break;
      default:
        restrictionPeriodDays = 0;
    }
  } else {
    restrictionType = 'none';
  }

  return {
    ...item,
    category: categoryMap[category] || '',
    restrictionType,
    restrictionPeriodDays,
    condition,
  };
});

function App() {
  const [query, setQuery] = useState('');
  const [filterType, setFilterType] = useState('');
  const [baseDate, setBaseDate] = useState(() =>
    new Date().toISOString().split('T')[0]
  );
  const { theme, toggleTheme } = useContext(ThemeContext);

  const formatDate = (date) => {
    const yyyy = date.getFullYear();
    const mm = String(date.getMonth() + 1).padStart(2, '0');
    const dd = String(date.getDate()).padStart(2, '0');
    return `${yyyy}${mm}${dd}`;
  };

  const results = useMemo(() => {
    const lower = query.toLowerCase();
    let filteredData = allData;
    if (filterType) {
      filteredData = filteredData.filter((item) => item.category === filterType);
    }
    if (!query) {
      return filteredData.map((item) => ({ ...item, matchInfo: null }));
    }
    return filteredData
      .map((item) => {
        const matchInfo = [];
        if (item.name.toLowerCase().includes(lower)) {
          matchInfo.push({ type: 'name', value: item.name });
        }
        const matchingAliases = (item.aliases || []).filter((alias) =>
          alias.toLowerCase().includes(lower)
        );
        if (matchingAliases.length > 0) {
          matchingAliases.forEach((alias) =>
            matchInfo.push({ type: 'alias', value: alias })
          );
        }
        const matchingKeywords = (item.keywords || []).filter((keyword) =>
          keyword.toLowerCase().includes(lower)
        );
        if (matchingKeywords.length > 0) {
          matchingKeywords.forEach((keyword) =>
            matchInfo.push({ type: 'keyword', value: keyword })
          );
        }
        if (matchInfo.length > 0) {
          return { ...item, matchInfo };
        }
        return null;
      })
      .filter(Boolean);
  }, [query, filterType]);

  return (
    <MuiThemeProvider theme={theme === 'light' ? lightTheme : darkTheme}>
      <div className="App p-4 sm:p-8 space-y-6 max-w-3xl mx-auto">
        {/*
            Grid    .
          1. 'grid' 'grid-cols-3'   3  .
          2. 'items-center'    .
        */}
        <header className="grid grid-cols-3 items-center">
          {/* 1() :  2     . */}
          <div></div>

          {/* 2() :   . */}
          <h1 className="text-lg sm:text-xl font-bold text-center">
              
          </h1>

          {/* 3() :    . */}
          <div className="flex justify-end">
            <IconButton
              onClick={toggleTheme}
              aria-label=" "
            >
              {theme === 'light' ? <FiMoon /> : <FiSun />}
            </IconButton>
          </div>
        </header>

        {/*
                .
          1. 'flex-wrap'         .
          2. ('flex-grow')     ,      .
        */}
        <div className="flex flex-wrap justify-between items-center gap-3">
          <div className="flex items-center gap-3 flex-grow">
            <FiSearch />
            <TextField
              variant="outlined"
              placeholder=" "
              value={query}
              onChange={(e) => setQuery(e.target.value)}
              size="small"
              className="flex-grow" // TextField    
            />
          </div>
          <TextField
            type="date"
            value={baseDate}
            onChange={(e) => setBaseDate(e.target.value)}
            size="small"
            inputProps={{ 'aria-label': ' ' }}
          />
        </div>

        {/*
            UI .
          1. 'flex-wrap'        .
          2. 'justify-center'     .
        */}
        <div className="flex justify-center">
          <FormControl
            size="small"
            aria-label=" "
            className={`flex-wrap ${
              query ? 'invisible' : ''
            } mt-3 sm:mt-0`}
          >
            <InputLabel shrink> </InputLabel>
            <ToggleButtonGroup
              exclusive
              size="small"
              value={filterType}
              onChange={(e, newType) => {
                setFilterType(newType || '');
              }}
              className="flex-wrap" //     .
            >
              <ToggleButton value=""></ToggleButton>
              <ToggleButton value=""></ToggleButton>
              <ToggleButton value=""></ToggleButton>
              <ToggleButton value=""></ToggleButton>
              <ToggleButton value=""></ToggleButton>
              <ToggleButton value=""></ToggleButton>
            </ToggleButtonGroup>
          </FormControl>
        </div>

        <ResultList
          results={results}
          query={query}
          baseDate={baseDate}
          formatDate={formatDate}
        />
      </div>
    </MuiThemeProvider>
  );
}

export default App;
